import React from "react";
import {connect} from "react-redux";
import "./style.css";

class Result extends React.Component {
    state = {clicked: 0};

    items = () => {
        return this.props.searchresult.state.items
    }

    clicked = () => {
        return this.state.clicked
    }

    fillPhoto = () => {
        let src = "./7.jpg"
        const {items, clicked} = this
        try {
            src = items()[clicked()].pagemap.cse_thumbnail[0].src;

        } catch (err) {
        }
        return <img alt="imgm" id="imgm" src={src}/>;
    };

    fillResults = () => {
        try {
            ["next", "prev", "or"].forEach(element => {
                document.getElementById(element).style.display = "flex";
            })

            const {items, clicked} = this

            return (
                <div id="result">
                    <div id="card" className="ui card">
                        <div className="image" id="photo">
                            {this.fillPhoto()}
                        </div>
                        <div className="content">
                            <a href={items()[clicked()].formattedUrl} className="header">
                                {items()[clicked()].title}
                            </a>

                            <div className="description">
                                {items()[clicked()].snippet}
                            </div>
                        </div>
                    </div>
                </div>
            );
        } catch (err) {
            return "";
        }
    };

    doOperation = (condition, newClicked) => {
        try {
            if (condition) {
                this.setState({clicked: newClicked});
            }
        } catch (err) {
        }
    }

    render() {
        const {doOperation, clicked, items} = this
        return (
            <div className="result">
                <div id="buttons">
                    <div className="ui buttons" id="uibuttons">
                        <button className="ui button" id="prev"
                                onClick={() => doOperation(clicked() - 1 >= 0, clicked() - 1)}>
                            poprzedni
                        </button>
                        <div className="or" id="or"></div>
                        <button
                            className="ui positive button"
                            id="next"
                            onClick={() => doOperation(clicked() + 1 < items().length, clicked() + 1)}
                        >
                            następny
                        </button>
                    </div>
                </div>
                {this.fillResults()}
            </div>
        );
    }
}

const mapStateToProps = ({searchresult}) => {
    return {
        searchresult
    };
};

export default connect(
    mapStateToProps,
    {}
)(Result);
